import axios from 'axios';
import { useEffect, useState } from 'react';
import { Link, useOutletContext } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { createAsyncMessage } from '../../slice/messageSlice';

function Cart() {
    const { cartData, getCart } = useOutletContext();
    const [loadingItems, setLoadingItems] = useState([])
    const dispatch = useDispatch()


    // console.log(cartData)
    const removeCartItem = async (id) => {
        try {
            const res = await axios.delete(
                `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${id}`
            )
            console.log(res)
            getCart();
        } catch (error) {
            console.log(error)
        }
        
    }
    
    const updateCartItem = async (item, quantity) => { // 調整購物車項目數量觸發
        const data = {
            data: {
                product_id: item.product_id,
                qty: quantity,
            }
        }
        setLoadingItems([...loadingItems, item.id])
        console.log([...loadingItems, item.id])
        try {
            const res = await axios.put(
                `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${item.id}`,
                data,
            )
            setLoadingItems(loadingItems.filter((loadingObject) => loadingObject !== item.id));
            dispatch(createAsyncMessage(res.data));
            console.log(res)
            getCart();
        } catch (error) {
            console.log(error)
            dispatch(createAsyncMessage(error.response.data));
            setLoadingItems(loadingItems.filter((loadingObject) => loadingObject !== item.id));
        }
        
    }

    return (
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-md-6 bg-white py-5" style={{minHeight: "calc(100vh - 56px - 76px)"}}>
                    <div className="d-flex justify-content-between">
                        <h2 className="mt-2">購物車內容</h2>
                    </div>
                    { !cartData?.carts?.length? <div className='p-2 my-5 text-light text-center' style={{backgroundColor:'gray'}}>尚未加入任何商品至購物車內</div>:''}
                    
                    { cartData?.carts?.map((item) => {
                        return (
                            <div className="d-flex mt-4 bg-light" key={item.id}>
                                <img src={ item.product.imageUrl } alt="" className='object-cover' style={{width: "120px"}} />
                                <div className="w-100 p-3 position-relative">
                                    <button type='button' className="position-absolute btn" style={{top: "10px", right: "10px"}} onClick={() => { removeCartItem(item.id)}}>
                                        <i className="bi bi-x-lg"></i>
                                    </button>
                                    <p className="mb-0 fw-bold">{ item.product.title }</p>
                                    <p className="mb-1 text-muted" style={{fontSize: "14px"}}>{ item.product.content }</p>
                                    <div className="d-flex justify-content-between align-items-center w-100">
                                        <div className="input-group w-50 align-items-center">
                                            <select name="" id="" 
                                            className='form-select' 
                                            value={item.qty}
                                            disabled={loadingItems.includes(item.id)}
                                            onChange={(e) => {
                                                updateCartItem(item, e.target.value * 1);
                                            }}>
                                                {
                                                [...(new Array(20))].map((i, num) => {
                                                    return (
                                                        <option value={num + 1} key={num}>{num + 1}</option>
                                                    )
                                                })
                                                }
                                                
                                            </select>
                                        </div>
                                        <p className="mb-0 ms-auto">NT$ { item.final_total }</p>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                    
                    {/* <table className="table mt-4 text-muted">
                        <tbody>
                            <tr>
                                <th scope="row" className="border-0 px-0 font-weight-normal">Lorem ipsum</th>
                                <td className="text-end border-0 px-0">NT$ {}</td>
                            </tr>
                            <tr>
                                <th scope="row" className="border-0 px-0 pt-0 font-weight-normal">Lorem ipsum</th>
                                <td className="text-end border-0 px-0 pt-0">NT$500</td>
                            </tr>
                        </tbody>
                    </table> */}
                    { !cartData?.carts?.length? '':<div className="d-flex justify-content-between mt-4">
                        <p className="mb-0 h4 fw-bold">總金額</p>
                        <p className="mb-0 h4 fw-bold">NT$ {cartData.final_total}</p>
                    </div>}
                    {!cartData?.carts?.length? 
                        <Link to={'/products'} className="btn btn-dark w-100 btn-block mt-4 rounded-0 py-3">去挑選商品</Link>
                        :<Link to={'/checkout'} className="btn btn-dark w-100 btn-block mt-4 rounded-0 py-3">結帳</Link>
                    }
                    
                </div>
            </div>
        </div>
    )
}

export default Cart;